@import url(https://fonts.googleapis.com/css2?family=Jaldi:wght@400;700&display=swap);
html,
body,
#root {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Jaldi", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.2rem;
}

header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: blue;
  color: white;
}

h1 span {
  display: inline-block;
}

h1 {
  margin: 1rem 1rem 0;
  font-size: 2rem;
}

#scoreboard {
  background-color: white;
  border: 1px solid white;
  border-radius: 1rem;
  margin: 1em;
  color: blue;
  padding: 0 3rem;
  align-self: stretch;
}

#scoreboard > * {
  margin: 0.5rem;
}

@media only screen and (min-width: 750px) {
  header {
    flex-direction: row;
  }

  h1 {
    margin: 0 0 0 3rem;
    font-size: 3rem;
  }

  #scoreboard > * {
    margin-left: 0;
    margin-right: 0;
  }
}

#game {
  /* I'm aware that using 'aspect-ratio' limits functionality to modern browsers */
  aspect-ratio: 1;
  display: block;
  margin: 0 auto;
  max-height: 100%;
  max-width: 100%;
  display: grid;
  transition: 0.3s linear all;
}

img {
  max-width: 100%;
  max-height: 100%;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 6px;
  transition: 0.1s linear all;
  opacity: 0;
}

@media (hover: hover) {
  .clickableImg:hover {
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
  }
}

.success {
  /* green hue */
  -webkit-filter: grayscale(100%) sepia(100%) hue-rotate(70deg);
          filter: grayscale(100%) sepia(100%) hue-rotate(70deg);
}

.failure {
  /* red hue */
  -webkit-filter: grayscale(100%) sepia(100%) hue-rotate(320deg);
          filter: grayscale(100%) sepia(100%) hue-rotate(320deg);
}

.App {
  text-align: center;

  height: 100vh;
  width: 100vw;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

main {
  max-height: 100%;
  overflow: hidden;
}

